<template>
	<view>
		<WatasiWatermark
			ref="watermark"
		/>
		<button @click="onClick">选择图片</button>
	</view>
</template>

<script>
	import WatasiWatermark from '@/uni_modules/watasi-watermark/components/watasi-watermark/watasi-watermark.vue';
	export default {
		components: {
			WatasiWatermark
		},
		data() {
			return {
				images: []
			};
		},
		methods: {
			onClick() {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						this.images = res.tempFilePaths
						this.$refs.watermark.start(res.tempFilePaths)
					}
				})
			}
		}
	}
</script>

